"use client"

import { useEffect, useState } from "react"
import { dataStore } from "@/lib/data-store"
import { DiscountCode } from "@/lib/types"

export default function AdminDiscountCodes() {
  const [discountCodes, setDiscountCodes] = useState<DiscountCode[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [filter, setFilter] = useState<'all' | 'active' | 'inactive'>('all')

  useEffect(() => {
    try {
      const allDiscountCodes = dataStore.getAllDiscountCodes()
      setDiscountCodes(allDiscountCodes)
    } catch (err) {
      setError("Failed to load discount codes")
      console.error("Discount codes error:", err)
    } finally {
      setLoading(false)
    }
  }, [])

  const filteredCodes = discountCodes.filter(code => {
    if (filter === 'all') return true
    return code.status === filter
  })

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(amount)
  }

  const formatDate = (date: Date) => {
    return new Date(date).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  const getStatusBadge = (status: string) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full"
    switch (status) {
      case 'active':
        return `${baseClasses} bg-green-100 text-green-800`
      case 'inactive':
        return `${baseClasses} bg-red-100 text-red-800`
      default:
        return `${baseClasses} bg-gray-100 text-gray-800`
    }
  }

  const formatDiscountValue = (type: string, value: number) => {
    if (type === 'percentage') {
      return `${value}%`
    }
    return formatCurrency(value)
  }

  const isExpired = (validUntil: Date) => {
    return new Date(validUntil) < new Date()
  }

  const isMaxedOut = (currentUses: number, maxUses: number) => {
    return currentUses >= maxUses
  }

  const getUsagePercentage = (currentUses: number, maxUses: number) => {
    return Math.round((currentUses / maxUses) * 100)
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900"></div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <p className="text-red-800">{error}</p>
      </div>
    )
  }

  return (
    <div>
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">Discount Codes</h1>
        <p className="text-gray-600 mt-2">Manage promotional codes and discounts</p>
      </div>

      {/* Filter Tabs */}
      <div className="mb-6">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8">
            {[
              { key: 'all', label: 'All Codes', count: discountCodes.length },
              { key: 'active', label: 'Active', count: discountCodes.filter(c => c.status === 'active').length },
              { key: 'inactive', label: 'Inactive', count: discountCodes.filter(c => c.status === 'inactive').length },
            ].map((tab) => (
              <button
                key={tab.key}
                onClick={() => setFilter(tab.key as any)}
                className={`py-2 px-1 border-b-2 font-medium text-sm ${
                  filter === tab.key
                    ? 'border-gray-900 text-gray-900'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                {tab.label} ({tab.count})
              </button>
            ))}
          </nav>
        </div>
      </div>

      {/* Add Code Button */}
      <div className="mb-6">
        <button className="bg-gray-900 text-white px-4 py-2 rounded-md hover:bg-gray-800 transition-colors">
          Create New Code
        </button>
      </div>

      {/* Discount Codes Table */}
      <div className="bg-white shadow rounded-lg overflow-hidden">
        {filteredCodes.length === 0 ? (
          <div className="p-8 text-center">
            <p className="text-gray-500">No discount codes found for the selected filter.</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Code
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Description
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Discount
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Usage
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Valid Period
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredCodes.map((code) => (
                  <tr key={code.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-gray-900">{code.code}</div>
                      <div className="text-xs text-gray-500">ID: {code.id.slice(-8)}</div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-sm text-gray-900 max-w-xs truncate">
                        {code.description}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-gray-900">
                        {formatDiscountValue(code.type, code.value)}
                      </div>
                      <div className="text-xs text-gray-500 capitalize">{code.type}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900">
                        {code.currentUses} / {code.maxUses}
                      </div>
                      <div className="w-full bg-gray-200 rounded-full h-2 mt-1">
                        <div
                          className={`h-2 rounded-full ${
                            isMaxedOut(code.currentUses, code.maxUses)
                              ? 'bg-red-500'
                              : getUsagePercentage(code.currentUses, code.maxUses) > 80
                              ? 'bg-yellow-500'
                              : 'bg-green-500'
                          }`}
                          style={{
                            width: `${Math.min(getUsagePercentage(code.currentUses, code.maxUses), 100)}%`
                          }}
                        ></div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <div>{formatDate(code.validFrom)}</div>
                      <div className="text-xs text-gray-500">to {formatDate(code.validUntil)}</div>
                      {isExpired(code.validUntil) && (
                        <div className="text-xs text-red-600 font-medium">Expired</div>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={getStatusBadge(code.status)}>
                        {code.status}
                      </span>
                      {isMaxedOut(code.currentUses, code.maxUses) && (
                        <div className="text-xs text-red-600 mt-1">Max uses reached</div>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                      <div className="flex space-x-2">
                        <button className="text-gray-600 hover:text-gray-900 transition-colors">
                          Edit
                        </button>
                        <button className="text-gray-600 hover:text-gray-900 transition-colors">
                          Copy
                        </button>
                        {code.status === 'active' ? (
                          <button className="text-red-600 hover:text-red-900 transition-colors">
                            Deactivate
                          </button>
                        ) : (
                          <button className="text-green-600 hover:text-green-900 transition-colors">
                            Activate
                          </button>
                        )}
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>

      {/* Summary Stats */}
      <div className="mt-8 grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Total Codes</h3>
          <p className="text-2xl font-bold text-gray-900">{filteredCodes.length}</p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Active Codes</h3>
          <p className="text-2xl font-bold text-green-600">
            {discountCodes.filter(c => c.status === 'active' && !isExpired(c.validUntil) && !isMaxedOut(c.currentUses, c.maxUses)).length}
          </p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Total Uses</h3>
          <p className="text-2xl font-bold text-blue-600">
            {filteredCodes.reduce((sum, c) => sum + c.currentUses, 0)}
          </p>
        </div>
        <div className="bg-white rounded-lg shadow p-4">
          <h3 className="text-sm font-medium text-gray-500">Avg Usage Rate</h3>
          <p className="text-2xl font-bold text-gray-900">
            {filteredCodes.length > 0 
              ? Math.round(
                  filteredCodes.reduce((sum, c) => sum + getUsagePercentage(c.currentUses, c.maxUses), 0) / filteredCodes.length
                )
              : 0
            }%
          </p>
        </div>
      </div>
    </div>
  )
}
